/*
 * Table Of Contents
 *
 * # SETTINGS & TOOLS-----------------------PROJECT SETTINGS & MIXINS
 * __settings.less..........................variables
 * __tool.menufont.less.....................font styles for menu bars
 * __tool.logo.less.........................background-image styles for logos
 *
 * # BASE-----------------------------------BARE UNCLASSED ELEMENTS
 * _dev.less................................development code
 * _base.less...............................bare, unclassed elements
 *
 * # OBJECTS--------------------------------REUSABLE STRUCTURAL PATTERNS
 * Runner...................................header and footer
 * Constrain................................keeps content at max width
 * GridFloat................................grid of items; legacy support
 * ListAction...............................list of actionable choices
 * ListStacked..............................row of stackable items (Sponsors, Passes, Speakers, etc.)
 * Tabs.....................................tabbed panel interface
 * Drawer...................................pull-out container
 *
 * # COMPONENTS-----------------------------SPECIFIC STYLE PATTERNS
 * Mast.....................................masthead for the site
 * SiteTitle................................area containg logo and site title and slogan
 * MenuMobile...............................mobile menu
 * MenuBar..................................navigation bar
 * Sitemap..................................sitemap for top (site) pages or main (internal) pages
 * Banner...................................full-width block with a background image
 * PageTitle................................title of entire page
 * ConfHed..................................promo for any year conference
 * Pass.....................................a listing of prices for one Pass Type
 * ProgramHn................................heading for TimeBlock
 * TimeBlock................................list of sessions for a single day
 * Speaker..................................(keynote) speaker profile
 * SupporterBlock...........................block containing one level of sponsors on Sponsors page
 * SocialList...............................list of social media icons
 * DateBlock................................list of important dates
 * Callout..................................textual block standing out from normal prose
 * Btn......................................buttons
 * LinkCamo.................................camouflaged links & elements
 * LinkCaps.................................link in all caps, like that of menus
 * Toggle...................................toggle button
 *   ## Block Section-y Components------------[description]
 *   ## Typographical Components--------------[description]
 *   ## User-Interactive Components-----------[description]
 *
 * # THEMES & HELPERS-----------------------LOCATION-DEPENDENT STYLES
 *
 * # OVERRIDES------------------------------ATOMS & HACKS
 * _atom.less...............................all uncategorized atoms
 * _shame.less..............................hacks
 */
